<template>
  <div class="section">
    <table>
      <tr>
        <td class="field">开始搬迁日期</td>
        <td colspan="2">{{list.move_at}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">开始安装日期</td>
        <td colspan="2">{{list.setup_at}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">导眼开钻时间</td>
        <td colspan="2">{{list.pilot_start_at}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">侧钻井开工日期</td>
        <td colspan="2">{{list.sidetracked_start_at}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">第一次开钻日期</td>
        <td colspan="2">{{list.drilling_at1}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">第一次完钻日期</td>
        <td colspan="2">{{list.completion_at1}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">第二次开钻日期</td>
        <td colspan="2">{{list.drilling_at2}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">第二次完钻日期</td>
        <td colspan="2">{{list.completion_at2}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">完钻日期</td>
        <td colspan="2">{{list.drilling_finish_at}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">完井日期</td>
        <td colspan="2">{{list.completion_finish_at}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">搬安与准备时间</td>
        <td colspan="2">{{list.move_prepare_at}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">钻井时间</td>
        <td colspan="2">{{list.drilling_period}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">完井时间</td>
        <td colspan="2">{{list.completion_period}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">建井时间</td>
        <td colspan="2">{{list.setup_period}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">中途测试时间</td>
        <td colspan="2">{{list.middle_test_at}}</td>
        <td></td>
      </tr>
      <tr>
        <td class="field">完钻井深(m)</td>
        <td>{{list.completion_depth}}</td>
        <td class="field">完钻垂深(m)</td>
        <td>{{list.completion_vertical_depth}}</td>
      </tr>
      <tr>
        <td class="field">油气层顶部井深(m)</td>
        <td>{{list.oil_top_depth}}</td>
        <td class="field">油气层底部井深(m)</td>
        <td>{{list.oil_bottom_depth}}</td>
      </tr>
      <tr>
        <td class="field">完井方法</td>
        <td colspan="3">{{list.completion_method}}</td>
      </tr>
      <tr>
        <td class="field">钻井台月(月) </td>
        <td colspan="3">{{list.drilling_num_month}}</td>
      </tr>
      <tr>
        <td class="field">钻井月速(m/月)</td>
        <td colspan="3">{{list.drilling_speed_month}}</td>
      </tr>
      <tr>
        <td class="field">平均机械钻速(m/h)</td>
        <td colspan="3">{{list.avg_penetration}}</td>
      </tr>
      <tr>
        <td class="field">井身质量</td>
        <td colspan="3">{{list.well_quality}}</td>
      </tr>
      <tr>
        <td class="field">固井质量</td>
        <td colspan="3">{{list.cementation_quality}}</td>
      </tr>
      <tr>
        <td class="field">钻井取心进尺(m)</td>
        <td>{{list.core_footage}}</td>
        <td class="field">平均收获率(%)</td>
        <td>{{list.avg_collection}}</td>
      </tr>
      <tr>
        <td class="field">工程报废进尺(m)</td>
        <td>{{list.fail_footage}}</td>
        <td class="field">返工进尺(m)</td>
        <td>{{list.remend_footage}}</td>
      </tr>
      <tr>
        <td class="field">进尺损失率(%)</td>
        <td colspan="3">{{list.footage_loss}}</td>
      </tr>
      <tr>
        <td class="field">完井总进尺(m)</td>
        <td colspan="3">{{list.completion_total_footage}}</td>
      </tr>
      <tr>
        <td class="field">钻井事故故障次数(次)</td>
        <td colspan="3">{{list.accident_num}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
  import request from "@/utils/request";
  export default {
    props: ["route"],
    data() {
      return {
        current: 1,
        list: {}
      }
    },
    mounted: function () {
      let data = this.route;
      request({
        url: "/data/query/wellcompletiondata",
        method: "post",
        data
      }).then(res => {
        this.list = res[0];
        // console.log(res);
        // this.createNumber(this.list.length);
      });
    },
    methods: {
      handleSizeChange() {

      },
      handleCurrentChange() {

      },
      // 生成表格序号
      createNumber(index) {
        return index + 1;
      }
    }
  }

</script>




